* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.main--offset {
	border        : 1px solid #CCD9E5;
	border-radius : 8px;
}

.container {
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	margin             : 0 auto;
	max-width          : 1256px;
	padding            : 0 14px;
	width              : 100%;

	@media screen and (min-width : $break-table) {
		padding : 0 16px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-title {

	font-size   : 22px;
	font-weight : 700;
	line-height : 30px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 46px;
	}
}

.section-head {
	margin-bottom : 24px;
	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.link-head {

	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	position    : relative;
	transition  : color .3s ease;

	&:before {

		bottom     : 4px;
		content    : "";
		height     : 1px;
		left       : 0;
		position   : absolute;
		transition : width .3s ease;
		width      : 0;
	}

	&:hover {

		transition : width .3s ease;

		&:before {
			width : 100%;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.scroll {
	height   : 100vh;
	overflow : hidden;
	position : relative;
	width    : 100vw;
}

