.destination {
	padding-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 48px;
	}

}

.destination__wrapper {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.destination-item {
	border-radius : 8px;
	grid-column   : 4 span;
	height        : 143px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		grid-column   : 6 span;
		height        : 176px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
		height      : 180px;
	}

	&:nth-of-type(1) {
		.destination-item__wrapper {
			background-image : url("../images/home/destination/destination-bg-tablet.jpg");


			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				background-image : url("../images/home/destination/destination-1-desktop.jpg");
			}
		}

		.destination-item__icon {
			right : 47px;
		}
	}

	&:nth-of-type(2) {
		.destination-item__wrapper {
			background-image : url("../images/home/destination/destination-bg-tablet.jpg");

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				background-image : url("../images/home/destination/destination-2-desktop.jpg");
			}
		}

		.destination-item__icon {
			right : 41px;
		}
	}

	&:nth-of-type(3) {
		.destination-item__wrapper {
			background-image : url("../images/home/destination/destination-bg-tablet.jpg");


			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				background-image : url("../images/home/destination/destination-3-desktop.jpg");
			}
		}

		.destination-item__icon {
			right : 25px;
		}
	}

	&:nth-of-type(4) {
		.destination-item__wrapper {
			background-image : url("../images/home/destination/destination-bg-tablet.jpg");
			background-size  : cover;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				background-image : url("../images/home/destination/destination-4-desktop.jpg");
			}
		}

		.destination-item__icon {
			right : 20px;
		}
	}


	&__wrapper {
		background-color  : #F6F6F6;
		background-repeat : no-repeat;
		background-size   : cover;
		border-radius     : 8px;
		display           : flex;
		flex-direction    : column;
		height            : 100%;
		padding           : 24px 24px;
		position          : relative;
		transition        : height .3s ease, background-size .3s ease;
		z-index           : 2;

		@media screen and (min-width : $break-table) {
			border-radius : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&__title {
		color       : $black;
		font-size   : 14px;
		font-weight : 700;
		line-height : 20px;
		opacity     : 1;
		transition  : visibility .3s ease, opacity .3s ease;
		visibility  : visible;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 20px;
			margin-top  : auto;
		}
	}

	@media screen and (min-width : $break-desktop) {
		&:hover {
			&:nth-of-type(1) {
				.destination-item__wrapper {
					background-image : url("../images/home/destination/destination-1-desktop-hover.jpg");
					background-size  : 288px 132px;
				}
			}

			&:nth-of-type(2) {
				.destination-item__wrapper {
					background-image : url("../images/home/destination/destination-2-desktop-hover.jpg");
					background-size  : 288px 132px;
				}
			}

			&:nth-of-type(3) {
				.destination-item__wrapper {
					background-image : url("../images/home/destination/destination-3-desktop-hover.jpg");
					background-size  : 288px 132px;
				}
			}

			&:nth-of-type(4) {
				.destination-item__wrapper {
					background-image : url("../images/home/destination/destination-4-desktop-hover.jpg");
					background-size  : 288px 132px;
				}
			}

			.destination-item__wrapper {
				border-radius : 12px;
				height        : 75%;
				transition    : height .3s ease, background-size .3s ease;
			}

			.destination-item__title {
				opacity    : 0;
				transition : visibility .3s ease, opacity .3s ease;
				visibility : hidden;
			}
		}
	}

	&__icon {
		position : absolute;
		right    : 40px;
		top      : 20px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	.b-btn--pink {
		border-radius   : 4px;
		bottom          : 20px;
		display         : flex;
		flex-direction  : column;
		font-size       : 10px;
		height          : 44px;
		justify-content : center;
		left            : 20px;
		line-height     : 14px;
		position        : absolute;
		width           : 77px;
		z-index         : 3;

		@media screen and (min-width : $break-table) {
			bottom : 24px;
			left   : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			border-radius   : 12px;
			bottom          : 0;
			font-size       : 14px;
			height          : 100%;
			justify-content : flex-end;
			left            : 0;
			line-height     : 20px;
			width           : 100%;
			z-index         : 1;
		}
	}
}
