.hero {
	padding : 16px 0 48px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 0 90px 0;
	}

	h1 {
		height     : 0;
		margin     : 0;
		opacity    : 0;
		visibility : hidden;
	}
}

.hero__item {
	display               : grid;
	grid-gap              : 20px;
	grid-template-columns : repeat(4, 1fr);
	padding-bottom        : 80px;
	position              : relative;

	@media screen and (min-width : $break-table) {
		grid-gap              : 0;
		grid-template-columns : repeat(12, 1fr);
		padding-bottom        : 100px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 0;
	}
}

.hero__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/5;
	}

	.hero__btn {
		bottom   : 0;
		left     : 0;
		position : absolute;

		@media screen and (min-width : $break-table) {
			left      : 50%;
			transform : translateX(-50%);
		}

		@media screen and (min-width : $break-desktop) {
			left      : unset;
			position  : unset;
			transform : unset;
		}

		.b-btn--pink {
			font-size   : 16px;
			line-height : 20px;
			padding     : 16px 16px;


			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}


}

.hero__wrap {
	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 30px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.hero__label {
	color         : $dark-grey;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 8px;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.hero__name {
	color       : $black;
	font-size   : 24px;
	font-weight : 700;
	line-height : 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 46px;
	}
}

.hero__image {
	grid-column : 4 span;
	text-align  : center;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
		grid-row    : 2/2;
		text-align  : center;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 5/9;
		grid-row    : 1/1
	}
}

.hero__about {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	grid-column : 4 span;
	line-height : 20px;
	opacity     : 0.9;

	@media screen and (min-width : $break-table) {
		grid-column : 8/13;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		grid-column : 9/13;
		line-height : 36px;
	}
}

.hero__swiper {
	margin : 0 -16px;
}

.hero__image {

}

.hero__item {

}

.swiper--hero {
	position : relative;

	.swiper-slide {
		position            : relative;
		transition-property : transform;
		transition-property : -webkit-transform;

	}


	.swiper-button {
		background-color : $white;
		border-radius    : 4px;
		height           : 40px;
		overflow         : hidden;
		position         : unset;
		width            : 40px;
		z-index          : 2;

		@media screen and (min-width : $break-table) {
			position  : absolute;
			top       : 50%;
			transform : translateY(-50%);
		}

		@media screen and (min-width : $break-desktop) {
			display   : block;
			position  : unset;
			top       : unset;
			transform : unset;
		}

		svg {
			//rect {
			//	stroke     : $grey-2;
			//	transition : stroke .3s ease;
			//}

			path {
				stroke     : $grey-2;
				transition : stroke .3s ease;
			}

		}

		&:hover {
			svg {
				//rect {
				//	stroke     : $pink;
				//	transition : stroke .3s ease;
				//}

				path {
					stroke     : $pink;
					transition : stroke .3s ease;
				}
			}
		}

		&-prev {
			left : 0;
		}

		&-next {
			right : 0;
		}
	}

	.slider-nav {
		position : absolute;
		right    : 0;
		top      : 100px;
		z-index  : 4;
		@media screen and (min-width : $break-table) {
			position : static;
		}

		@media screen and (min-width : $break-desktop) {
			align-items : center;
			bottom      : 60px;
			display     : flex;
			position    : absolute;
			right       : 0;
			top         : unset;
		}
	}

}

.swiper-fade .swiper-slide {

	transition-property : opacity;
}

.swiper-fade {
	.hero__label {
		opacity                  : 0;
		transform                : translateY(15px) scale(0.95);
		transition               : transform .3s ease, opacity .3s ease, -webkit-transform .3s ease;
		-webkit-transition-delay : 0s;
		transition-delay         : 0s;
	}

	.hero__name {
		opacity          : 0;
		transform        : translateY(15px) scale(0.95);
		transition       : transform .3s ease, opacity .3s ease, -webkit-transform .3s ease;
		transition-delay : 0s;
	}

	.hero__image {
		opacity    : 0;
		transform  : scale(0.91);
		transition : transform .6s ease, opacity .6s ease, -webkit-transform .6s ease;
	}

	.hero__btn {
		opacity          : 0;
		transform        : translateY(15px) scale(0.95) translateX(-50%);
		transition       : transform .3s ease, opacity .3s ease, -webkit-transform .3s ease;
		transition-delay : 0s;

		@media screen and (min-width : $break-desktop) {
			transform : translateY(15px) scale(0.95) translateX(0);
		}
	}

	.hero__about {
		opacity          : 0;
		transform        : translateY(15px) scale(0.95);
		transition       : transform .3s ease, opacity .3s ease, -webkit-transform .3s ease;
		transition-delay : 0s;
	}
}

.swiper-fade .swiper-slide.swiper-slide-active {

	.hero__wrap:nth-child(1) {
		.hero__label {
			-webkit-transition-delay : .1s;
			transition-delay         : .1s;
		}

		.hero__name {
			opacity                  : 1;
			transform                : translateY(0) scale(1);
			-webkit-transition-delay : .25s;
			transition-delay         : .25s;
		}
	}

	.hero__wrap:nth-child(2) {
		.hero__label {
			-webkit-transition-delay : .4s;
			transition-delay         : .4s;
		}

		.hero__name {
			-webkit-transition-delay : .55s;
			transition-delay         : .55s;
		}
	}


	.hero__label {
		opacity   : 1;
		transform : translateY(0) scale(1);
	}

	.hero__image {
		opacity   : 1;
		transform : scale(1);
	}

	.hero__name {
		opacity          : 1;
		transform        : translateY(0) scale(1);
		transition-delay : .25s;
	}

	.hero__btn {
		opacity          : 1;
		transform        : translateY(0) scale(1);
		transition-delay : .65s;

		@media screen and (min-width : $break-table) {
			transform : translateY(0) scale(1) translateX(-50%);
		}

		@media screen and (min-width : $break-desktop) {
			transform : translateY(0) scale(1) translateX(0);
		}

	}

	.hero__about {
		opacity          : 1;
		transform        : translateY(0) scale(1);
		transition-delay : .25s;
	}

}

@keyframes slide-progress {
	0% {
		stroke-dashoffset : 150
	}

	100% {
		stroke-dashoffset : 0
	}
}

.swiper-progress {
	left     : 0;
	position : unset;
	top      : 0;
	z-index  : 1;
}

.swiper-progress__background {
	fill         : none;
	stroke       : transparent;
	stroke-width : 2
}

.swiper-progress__line {
	--slider-delay    : 15000ms;
	fill              : none;
	stroke            : #E61F4D;
	stroke-dasharray  : 149;
	stroke-dashoffset : 150;
	stroke-linecap    : round;
	stroke-width      : 2;
}

.swiper-progress.is-active .swiper-progress__line {
	animation : slide-progress var(--slider-delay) cubic-bezier(0.3, 0, 0.3, 1);
}
