.footer {
	background-color : $light-grey-2;
}

.footer__top {
	border-bottom : 1px solid #9999994C;
	padding       : 32px 0 48px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
		padding         : 40px 0 47px 0;
	}
}

.logo--footer {
	.logo__text {
		display : block;
	}

	.logo__icon {
		height : 27px;
		width  : 142px;
	}
}


.footer__middle {
	border-bottom : 1px solid #9999994C;
	padding       : 48px 0;

}

.footer__nav {
	display    : flex;
	flex-wrap  : wrap;
	margin-top : 48px;
	row-gap    : 16px;
	width      : 100%;

	@media screen and (min-width : $break-table) {
		flex-wrap       : unset;
		gap             : 0;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {
		justify-content : flex-end;
		margin-top      : 0;
		width           : auto;
	}

	li {
		width : 50%;

		@media screen and (min-width : $break-table) {
			width : auto;
		}

		&:not(:first-child) {

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-left : 48px;
			}
		}


		a {
			color       : $dark-grey;
			font-size   : 14px;
			font-weight : 700;
			line-height : 20px;
		}
	}
}

.footer__bottom {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	padding        : 24px 0;


	@media screen and (min-width : $break-table) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact__wrapper {
	display               : grid;
	grid-row-gap          : 32px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 20px;
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 0;
		grid-template-columns : repeat(13, 1fr);
	}
}

.contact__item {
	grid-column : 4 span;

	&:nth-of-type(1) {

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/6;
		}
	}

	&:nth-of-type(2) {

		@media screen and (min-width : $break-desktop) {
			grid-column : 7/9;
		}
	}

	&:nth-of-type(3) {

		@media screen and (min-width : $break-desktop) {
			grid-column : 11/14;
		}
	}
}

.contact__wrap {
	align-items   : center;
	display       : flex;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		align-items    : flex-start;
		flex-direction : column;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact__icon {
	height        : 32px;
	margin-bottom : 12px;
	width         : 32px;

	@media screen and (min-width : $break-table) {
		height : 40px;
		width  : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact__name {
	color       : $grey;
	font-size   : 12px;
	font-weight : 400;
	line-height : 18px;
	margin-left : 12px;

	@media screen and (min-width : $break-table) {
		margin-left : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact__info {
	align-items    : flex-start;
	color          : $dark-grey;
	display        : flex;
	flex-direction : column;
	font-size      : 14px;
	font-weight    : 700;
	line-height    : 20px;
	max-width      : 219px;

	@media screen and (min-width : $break-table) {
		max-width : none;
	}

	a {
		display : block;
	}
}

.contact__subinfo {
	color       : $grey;
	font-size   : 12px;
	font-weight : 400;
	line-height : 18px;
	margin-top  : 4px;

	@media screen and (min-width : $break-table) {
		margin-top : 8px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.footer__link {
	color       : $grey;
	font-size   : 12px;
	font-weight : 400;
	line-height : 18px;

	&:not(:last-child) {
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}
	}

	a {
		color       : $grey;
		font-size   : 12px;
		font-weight : 400;
		line-height : 18px;
	}
}
