.popular {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0;
	}
}

.popular__wrapper {
	min-height : 400px;
	position   : relative;

	@media screen and (min-width : $break-table) {
		min-height : auto;
	}
}

.popular__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.popular__wrap {
	align-items : center;
	display     : flex;

	.slider-nav {
		display : none;

		@media screen and (min-width : $break-table) {
			display     : flex;
			margin-left : 12px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 24px;
		}
	}

	.b-btn--grey {
		bottom    : 0;
		left      : 50%;
		position  : absolute;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			position  : unset;
			transform : unset;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.popular__slider {
	margin : 0 -4px;

	@media screen and (min-width : $break-table) {
		margin : 0 -13px;
	}

	@media screen and (min-width : $break-desktop) {
		margin     : 0 -12px;
		min-height : 396px;
		overflow   : hidden;
	}
}

.product-card {
	border-radius : 8px;
	margin        : 0 4px;
	min-height    : 251px;
	position      : relative;
	transition    : all .3s ease;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		margin        : 0 13px;
		min-height    : 321px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 348px;
		margin : 0 12px;
	}

	.b-btn--pink {
		border-radius   : 8px;
		bottom          : 0;
		display         : flex;
		flex-direction  : column;
		font-size       : 10px;
		height          : 30%;
		justify-content : flex-end;
		left            : 0;
		line-height     : 14px;
		position        : absolute;
		transition      : bottom .3s ease, height .3s ease;
		width           : 100%;
		z-index         : 1;

		@media screen and (min-width : $break-table) {
			border-radius : 12px;
			font-size     : 14px;
			line-height   : 20px;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : 46px;
			height : calc(33% - 46px);
		}
	}

	@media screen and (min-width : $break-desktop) {

		&:hover {
			min-height : 390px;
			transition : all .3s ease;

			.b-btn--pink {
				bottom     : 0;
				height     : 100%;
				transition : bottom .3s ease, height .3s ease;
			}
		}
	}
}

.product-card__wrapper {
	background-color : $light-grey-2;
	border-radius    : 8px;
	padding          : 12px 12px;
	position         : relative;
	z-index          : 2;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;

	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 24px;
	}
}

.product-card__type {
	color          : $grey;
	font-size      : 9px;
	font-weight    : 400;
	letter-spacing : 0.03em;
	line-height    : 12px;
	margin-bottom  : 8px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 11px;
		line-height : 18px;
	}
}

.product-card__img {
	height        : 100px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		height : 140px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 180px;
	}

	img {
		height     : 100%;
		object-fit : contain;
		width      : 100%;
	}
}

.product-card__name {
	margin-bottom : 8px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
	}

	div {
		&:first-child {
			color         : $black;
			font-size     : 10px;
			font-weight   : 700;
			line-height   : 14px;
			margin-bottom : 4px;

			@media screen and (min-width : $break-table) {
				font-size   : 14px;
				line-height : 20px;
			}

			@media screen and (min-width : $break-desktop) {
				font-size : 16px;
			}
		}

		&:last-child {
			color       : $grey;
			font-size   : 9px;
			font-weight : 400;
			line-height : 12px;

			@media screen and (min-width : $break-table) {
				font-size   : 12px;
				line-height : 18px;
			}

			@media screen and (min-width : $break-desktop) {
				font-size   : 14px;
				line-height : 20px;
			}
		}
	}
}

.product-card__status {
	align-items    : center;
	display        : flex;
	font-size      : 9px;
	font-weight    : 400;
	letter-spacing : 0.03em;
	line-height    : 12px;
	text-transform : uppercase;

	&:before {
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : inline-block;
		height            : 15px;
		margin-right      : 6px;
		width             : 15px;

		@media screen and (min-width : $break-table) {
			height       : 18px;
			margin-right : 8px;
			width        : 18px;
		}
	}

	@media screen and (min-width : $break-table) {
		font-size   : 11px;
		line-height : 18px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--no {
		color : $pink;

		&:before {
			background-image : url("../images/icon/no.svg");
		}
	}

	&--yes {
		color : $green;

		&:before {
			background-image : url("../images/icon/yes.svg");
		}
	}
}


.swiper--popular {
	overflow : visible;

	.swiper-slide {
		width : 48%;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			width : 25%;
		}
	}

	.swiper-pagination {
		
		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : $break-desktop) {
		}
	}
}
