// -----------------------------------------------
//  Form config
// -----------------------------------------------
// Error
$form-label-color  : #A7A8B1;
$form-label-size   : 12px;
$form-label-weight : 400;
$form-label-margin : 0 0 10px;
$form-error-color  : $red;
$form-error-size   : 13px;
$form-error-weight : 500;

// Hint
$form-hint-color   : #444;
$form-hint-size    : 12px;
$form-hint-weight  : 400;
$form-hint-style   : italic;
$form-hint-margin  : 5px 0 0;

// Grid
$form-grid         : 7px;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
.form__row {
	display       : flex;
	margin-bottom : 24px;
	position      : relative;
	width         : 100%;

	&:first-child {
		margin-top : 0;
	}

	input[type="text"],
	input[type="email"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="password"],
	select,
	textarea {
		vertical-align : top;
		width          : 100%;
	}

	> label:not(.checkbox-style) {
		align-items     : center;
		color           : $form-label-color;
		cursor          : pointer;
		display         : flex;
		flex-flow       : row nowrap;
		font-size       : $form-label-size;
		font-weight     : $form-label-weight;
		justify-content : space-between;
		line-height     : 1.2;
		margin          : $form-label-margin;

		> span {
			order : 0;
		}

		.error-message {
			margin-left : 10px;
			margin-top  : 0;

			+ span {
				color : $red;
			}
		}
	}
}

.form--feedback {
	.form__input {
		padding : 24px 14px 14px 14px;
	}

	.form__row {
		display               : grid;
		grid-gap              : 16px;
		grid-template-columns : repeat(4, 1fr);
		margin-bottom         : 16px;

		@media screen and (min-width : $break-table) {
			grid-template-columns : repeat(6, 1fr);
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.form__wrap {
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {
			grid-column : 3 span;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.form__area {
		height : 114px;
		resize : none;

		@media screen and (min-width : $break-table) {
			height : 96px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&::placeholder {
			color       : #828281;
			font-size   : 16px;
			line-height : 22px;
		}
	}

	.form__bottom {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column-reverse;

		@media screen and (min-width : $break-table) {
			align-items    : center;
			flex-direction : row;
		}

		@media screen and (min-width : $break-desktop) {

		}

		span {
			color         : $grey;
			display       : block;
			font-size     : 12px;
			font-weight   : 400;
			line-height   : 16px;
			margin-bottom : 16px;


			@media screen and (min-width : $break-table) {
				margin-bottom : 0;
				margin-left   : 16px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	.form__wrapper {
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.b-btn--pink {
		padding : 16px 16px;
	}
}

.form__name {
	color          : $grey;
	display        : block;
	font-size      : 16px;
	font-weight    : 400;
	left           : 17px;
	line-height    : 20px;
	pointer-events : none;
	position       : absolute;
	top            : 50%;
	transform      : translateY(-50%);
	transition     : top .3s ease-in-out, transform .3s ease-in-out, left .3s ease-in-out;
	z-index        : 1;
}

.form__input:active ~ .form__name,
.form__input:focus ~ .form__name, {
	color       : #323232;
	font-size   : 12px;
	left        : 14px;
	line-height : 12px;
	top         : 18px;
	//transform   : scale(.8);
}

.form__input--active ~ .form__name {
	color       : #323232;
	//transform : scale(.8);
	font-size   : 12px;
	left        : 14px;
	line-height : 12px;
	top         : 18px;
}

.form__input.form__input--active {

}

.form__group {
	position : relative;
}

.form__input {
	position : relative;
	z-index  : 1;
}

.form__area {
	background-color : #FFF;
	width            : 100%;
}

.form__title {
	color         : #131313;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.form__top {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {


	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.fo
.form__hint {
	color       : $form-hint-color;
	font-size   : $form-hint-size;
	font-style  : $form-hint-style;
	font-weight : $form-hint-weight;
	margin      : $form-hint-margin;
	position    : relative;
	z-index     : 2;
}

.form__error {
	color       : $form-error-color;
	font-size   : $form-error-size;
	font-weight : $form-error-weight;
	margin-top  : 3px;
	position    : relative;
	z-index     : 3;
}

.form-password {
	position : relative;

	input[type="text"],
	input[type="password"] {
		padding-right : 50px;
		position      : relative;
		z-index       : 1;
	}
}

.form-password__button {
	background                  : transparent;
	border                      : 0;
	bottom                      : 0;
	-moz-box-sizing             : border-box;
	-webkit-box-sizing          : border-box;
	box-sizing                  : border-box;
	cursor                      : pointer;
	opacity                     : .3;
	outline                     : 0;
	padding                     : 0;
	position                    : absolute;
	right                       : 0;
	-webkit-tap-highlight-color : transparent;
	top                         : 0;
	transition                  : opacity .3s ease;
	vertical-align              : middle;
	width                       : 42px;
	z-index                     : 10;

	i {
		position       : relative;
		vertical-align : middle;
		z-index        : 1;
	}

	&:after {
		background : #000;
		border-top : 1px solid #FFF;
		content    : '';
		display    : block;
		height     : 1px;
		left       : 9px;
		position   : absolute;
		top        : 24px;
		transform  : rotate(-30deg);
		transition : opacity .3s ease;
		width      : 24px;
		z-index    : 10;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		&:hover {
			opacity : 1;
		}
	}

	&.active {
		opacity : 1;

		&:after {
			opacity : 0;
		}
	}
}
