// Arrow
.swiper-button-prev,
.swiper-button-next {
	background-color            : $white;
	border-radius               : 4px;
	cursor                      : pointer;
	display                     : inline-block;
	flex                        : 0 0 40px;
	height                      : 40px;
	outline                     : 0;
	overflow                    : hidden;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : opacity .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 40px;
	z-index                     : 2;

	svg {
		rect {
			&:first-child {
				stroke     : $grey-2;
				transition : stroke .3s ease;
			}
		}

		path {
			stroke     : $grey-2;
			transition : stroke .3s ease;
		}

	}

	&:hover {
		svg {
			rect {
				&:first-child {
					stroke     : $pink;
					transition : stroke .3s ease;
				}
			}

			path {
				stroke     : $pink;
				transition : stroke .3s ease;
			}
		}
	}

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

.swiper-button-prev {
	margin-right : 6px;

}

.swiper-button-next {
	margin-left : 6px;
}

.slider__pagination {
	display         : flex;
	flex-flow       : row nowrap;
	justify-content : space-between;
}
