// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 700;
$btn-line-height           : 20px;
$btn-border-width          : 1px;
$btn-padding               : 9px 16px 9px 16px;
$btn-border-radius         : 4px;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR


// Red
$btn-pink-bg               : #E61F4D;
$btn-pink-color            : #FFF;
$btn-pink-color-outline    : $btn-pink-bg;
$btn-pink-bg-hover         : #D80B3A;
$btn-pink-color-hover      : #FFF;

// Grey
$btn-grey-bg               : #EEE;
$btn-grey-color            : #333;
$btn-grey-color-outline    : #121214;
$btn-grey-bg-hover         : #EEE;
$btn-grey-color-hover      : #E61F4D;

// Link
$btn-link-bg               : transparent;
$btn-link-color            : #18A3FA;
$btn-link-bg-hover         : #F7F7F7;
$btn-link-color-hover      : darken(#18A3FA, 10%);


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR
	// Red
	&--pink {
		background-color : $btn-pink-bg;
		color            : $btn-pink-color;
		font-size        : 12px;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-pink-bg-hover;
			color            : $btn-pink-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-pink-bg;
				color            : $btn-pink-color;
			}
		}

		@media screen and (min-width : $break-table) {
			font-size : 14px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	// Black
	&--grey {
		background-color : $btn-grey-bg;
		color            : $btn-grey-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-grey-bg-hover;
			color            : $btn-grey-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-grey-bg;
				color            : $btn-grey-color;
			}
		}

		@media screen and (min-width : $break-table) {
			font-size : 14px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}


	// OUTLINE
	&--outline-pink {
		background-color : transparent;
		border           : 1px solid $btn-pink-bg;
		color            : $btn-pink-color-outline;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-pink-bg-hover;
			color            : $btn-pink-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-pink-bg;
				color            : $btn-pink-color-outline;
			}
		}
	}

	// Black
	&--outline-black {
		background-color : transparent;
		border           : 1px solid $btn-grey-bg;
		color            : $btn-grey-color-outline;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-grey-bg-hover;
			color            : $btn-grey-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-grey-bg;
				color            : $btn-grey-color-outline;
			}
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
