@font-face {
	font-family  : 'MicrosoftYaHei';
	src          : url('../fonts/MicrosoftYaHei.woff2') format('woff2'),
	url('../fonts/MicrosoftYaHei.woff') format('woff'),
	url('../fonts/MicrosoftYaHei.ttf') format('truetype');
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}

@font-face {
	font-family  : 'MicrosoftYaHei';
	src          : url('../fonts/MicrosoftYaHei-Bold.woff2') format('woff2'),
	url('../fonts/MicrosoftYaHei-Bold.woff') format('woff'),
	url('../fonts/MicrosoftYaHei-Bold.ttf') format('truetype');
	font-weight  : 700;
	font-style   : normal;
	font-display : swap;
}
