.feedback {
	padding : 32px 0 64px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0 96px 0;
	}
}

.feedback__wrap {
	margin-bottom : 48px;
}

.feedback__title {
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
	}

}

.feedback__text {
	color       : $dark-grey;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.feedback__wrapper {
	align-items           : center;
	background-color      : $light-grey-2;
	display               : grid;
	grid-gap              : 32px;
	grid-template-columns : repeat(4, 1fr);
	margin                : 0 -16px;
	padding               : 16px 14px;

	@media screen and (min-width : $break-table) {
		border-radius         : 12px;
		grid-gap              : 0;
		grid-template-columns : repeat(12, 1fr);
		margin                : 0;
		padding               : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.feedback__left {
	grid-column : 4 span;
	grid-row    : 2/2;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
		padding     : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/9;
		grid-row    : unset;
		padding     : 64px 64px;
	}
}

.feedback__right {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 9/13;
	}

	img {
		border-radius : 12px;
		object-fit    : cover;
		width         : 100%;
	}
}

