// -----------------------------------------------
//  Form config
// -----------------------------------------------

$input-background          : #FFF;
$input-border              : 1px solid transparent;
$input-padding             : 14px 24px;
$input-radius              : 4px;

$input-font-size           : 14px;
$input-font-weight         : 400;
$input-line-height         : 1.25;
$input-color               : $black;
$input-transition          : border-color .3s ease;

// Placeholder
$input-placeholder         : $grey;

// Focus
$input-border-focus        : transparent;

// Error
$input-border-error        : $red;

// Disabled
$input-border-disabled     : #BCBCBC;
$input-background-disabled : #F9F9F9;
$input-color-disabled      : #939393;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
input[type="text"],
input[type="email"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="password"],
select,
textarea {
	-webkit-appearance : none;
	background-color   : $input-background;
	border             : $input-border;
	border-radius      : $input-radius;
	box-sizing         : border-box;
	color              : $input-color;
	font-size          : $input-font-size;
	font-weight        : $input-font-weight;
	height             : auto;
	line-height        : $input-line-height;
	max-width          : 100%;
	outline            : 0;
	padding            : $input-padding;
	transition         : $input-transition;
	@include placeholder() {
		color       : $input-placeholder;
		font-size   : 0;
		font-weight : $input-font-weight;
	}

	&:focus {
		border-color : $input-border-focus;
	}

	&.error {
		border-color : $input-border-error;
		color        : $red;
	}

	&.disabled,
	&[disabled] {
		background-color : $input-background-disabled;
		border-color     : $input-border-disabled;
		color            : $input-color-disabled;
		cursor           : default;
	}

	&[readonly] {
		background-color : $input-background-disabled;
		border-color     : $input-border-disabled;
		color            : $input-color-disabled;
		cursor           : default;
	}
}

select {
	background-image    : url('../images/select.svg');
	background-position : 100% 50%;
	background-repeat   : no-repeat;
	padding-right       : 35px;
}

input[type="radio"],
input[type="checkbox"] {
	display        : inline-block;
	vertical-align : middle;
}

textarea {
	height   : auto;
	overflow : auto;
}

.error-message {
	color      : $red;
	margin-top : 5px;
	order      : 1;
}

label.checkbox-style,
.checkbox-style {
	color                       : #A7A8B1;
	cursor                      : pointer;
	display                     : block;
	font-size                   : 13px;
	font-weight                 : 400;
	height                      : 16px;
	line-height                 : 1.3;
	margin                      : 0;
	outline                     : 0;
	overflow                    : hidden;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	user-select                 : none;

	@media screen and (min-width : $break-table) {
		height : 24px;
	}

	> span {
		color          : $grey;
		display        : inline-block;
		padding        : 3px 0 2px 35px;
		position       : relative;
		vertical-align : top;

		&:before {
			border        : 1px solid #CCD9E5;
			border-radius : 100%;
			box-sizing    : border-box;
			content       : '';
			height        : 16px;
			left          : 0;
			position      : absolute;
			top           : 0;
			transition    : border-color .3s ease, background-color .3s ease;
			width         : 16px;
			z-index       : 1;

			@media screen and (min-width : $break-table) {
				height : 24px;
				width  : 24px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		&:after {
			background-color    : #00417F;
			background-image    : url("../images/icon/check-small2.svg");
			background-position : center center;
			background-repeat   : no-repeat;
			background-size     : 10px 10px;
			border-radius       : 100%;
			content             : "";
			height              : 14px;
			left                : 1px;
			opacity             : 0;
			position            : absolute;
			top                 : 1px;
			transition          : opacity .3s ease;
			width               : 14px;
			z-index             : 5;

			@media screen and (min-width : $break-table) {
				background-size : 16px 16px;
				height          : 22px;
				width           : 22px;
			}
		}

		&:hover {
			&:before {
				background-image    : url("../images/icon/check-small-blue.svg");
				background-position : center center;
				background-repeat   : no-repeat;
				border              : 1px solid #00417F;
			}
		}
	}

	input[type="checkbox"] {
		height                      : 1px;
		left                        : -30px;
		outline                     : 0;
		position                    : absolute;
		-webkit-tap-highlight-color : transparent;
		top                         : 0;
		width                       : 1px;

		&:checked {
			~ span {
				&:before {
				}

				&:after {
					opacity : 1;
				}
			}
		}

		&.disabled,
		&[disabled] {
			~ span {
				&:after {
					background : #E3E3E3;
				}
			}
		}
	}
}
