.header {
	background-color : $white;
}

.header__container {
	border-bottom : 1px solid $light-grey;
	padding       : 11px 14px;

	@media screen and (min-width : $break-table) {
		padding : 15px 16px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 16px;
	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.header__wrap {
	align-items   : center;
	border-right  : 1px solid $light-grey;
	display       : flex;
	padding-right : 16px;

	@media screen and (min-width : $break-table) {
		padding-right : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.logo {
	align-items : center;
	display     : flex;

	&__icon {
		height : 20px;
		width  : 91px;
		@media screen and (min-width : $break-table) {
			height : 30px;
			width  : 137px;
		}

		@media screen and (min-width : $break-desktop) {
			width : 142px;
		}
	}

	&__text {
		color       : $grey;
		display     : none;
		font-size   : 9px;
		font-weight : 400;
		line-height : 12px;
		margin-left : 12px;
		max-width   : 134px;
		position    : relative;
		top         : 4px;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}
	}
}

.nav {
	margin-left : 24px;

	&__list {
		align-items : center;
		display     : flex;
	}

	&__item {
		position : relative;
		z-index  : 4;
		
		&:not(:first-child) {
			margin-left : 12px;
		}

		&:hover {
			.nav__submenu {
				opacity    : 1;
				visibility : visible;
			}
		}
	}

	&__link {
		align-items      : center;
		background-color : transparent;
		border           : none;
		color            : $dark-grey;
		display          : inline-flex;
		font-size        : 12px;
		font-weight      : 400;
		line-height      : 18px;
		padding          : 6px 6px;

		svg {
			margin-left : 4px;
		}
	}

	&__submenu {
		left        : 0;
		opacity     : 0;
		padding-top : 59px;
		position    : absolute;
		top         : 0;
		transition  : visibility .3s ease, opacity .3s ease;
		visibility  : hidden;
		z-index     : 3;

		li {
			background-color : #F6F6F6;
			min-width        : 312px;
			padding          : 14px 24px;
			transition       : color .3s ease, background-color .3s ease;

			&:hover {
				background-color : $light-pink-2;

				a {
					color : $pink;
				}
			}

			a {
				align-items : center;
				color       : $dark-grey;
				display     : flex;
				font-size   : 14px;
				line-height : 20px;

				span {
					padding-left : 16px;
				}
			}
		}
	}
}

.header__group {
	.b-btn {
		@media screen and (min-width : $break-table) {
			margin-left : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 32px;
		}
	}
}

.header__phone {
	color       : $dark-grey;
	display     : none;
	font-size   : 14px;
	font-weight : 700;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		display : inline-block;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__panel {
	display : none;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}
